'use client';

import { useState } from 'react';
import { bundles, products, getBundleAvailability, formatCurrencyPrecise, getProductById } from '@/data/mockData';
import { Bundle } from '@/types';

export default function BundlesPage() {
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [selectedBundle, setSelectedBundle] = useState<string | null>(null);

  // Calculate availability for a bundle
  const getAvailability = (bundle: Bundle) => {
    return getBundleAvailability(bundle);
  };

  // Calculate stats
  const activeBundles = bundles.filter(b => b.isActive).length;
  const fullyAvailable = bundles.filter(b => {
    const avail = getAvailability(b);
    return avail.available > 10;
  }).length;
  const limitedStock = bundles.filter(b => {
    const avail = getAvailability(b);
    return avail.available > 0 && avail.available <= 10;
  }).length;
  const outOfStock = bundles.filter(b => {
    const avail = getAvailability(b);
    return avail.available === 0;
  }).length;
  const bundleSalesMTD = 4280;

  const getStatusBadge = (bundle: Bundle) => {
    const avail = getAvailability(bundle);
    if (avail.available === 0) {
      return { text: 'Out of Stock', color: 'red' };
    } else if (avail.available <= 10) {
      return { text: 'Limited', color: 'amber' };
    }
    return { text: 'In Stock', color: 'emerald' };
  };

  const getProductName = (productId: string) => {
    const product = products.find(p => p.id === productId);
    return product?.name || productId;
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Bundles</h1>
          <p className="text-sm text-slate-400">Product bundles and kits with component tracking</p>
        </div>
        <div className="flex items-center gap-3">
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <i className="fas fa-sync-alt text-sm"></i>
            Sync Availability
          </button>
          <button
            onClick={() => setShowCreateModal(true)}
            className="px-4 py-2 bg-amber-600 hover:bg-amber-500 text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <i className="fas fa-plus text-sm"></i>
            New Bundle
          </button>
        </div>
      </div>

      {/* Stats Row */}
      <div className="grid grid-cols-5 gap-4">
        <div className="bg-slate-800/50 backdrop-blur border border-amber-500/30 rounded-xl p-4" style={{ boxShadow: '0 0 20px rgba(251, 191, 36, 0.15)' }}>
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-amber-500/20 flex items-center justify-center">
              <i className="fas fa-layer-group text-amber-400"></i>
            </div>
            <div>
              <div className="text-2xl font-bold text-amber-400">{activeBundles}</div>
              <div className="text-xs text-slate-400">Active Bundles</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-emerald-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-emerald-500/20 flex items-center justify-center">
              <i className="fas fa-check-circle text-emerald-400"></i>
            </div>
            <div>
              <div className="text-2xl font-bold text-emerald-400">{fullyAvailable}</div>
              <div className="text-xs text-slate-400">Fully Available</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-amber-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-amber-500/20 flex items-center justify-center">
              <i className="fas fa-exclamation-triangle text-amber-400"></i>
            </div>
            <div>
              <div className="text-2xl font-bold text-amber-400">{limitedStock}</div>
              <div className="text-xs text-slate-400">Limited Stock</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-red-500/30 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-red-500/20 flex items-center justify-center">
              <i className="fas fa-times-circle text-red-400"></i>
            </div>
            <div>
              <div className="text-2xl font-bold text-red-400">{outOfStock}</div>
              <div className="text-xs text-slate-400">Out of Stock</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-slate-700/50 flex items-center justify-center">
              <i className="fas fa-dollar-sign text-slate-400"></i>
            </div>
            <div>
              <div className="text-2xl font-bold text-white">${bundleSalesMTD.toLocaleString()}</div>
              <div className="text-xs text-slate-400">Bundle Sales (MTD)</div>
            </div>
          </div>
        </div>
      </div>

      {/* Bundle Type Explanation */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
        <div className="flex items-center gap-6">
          <div className="flex items-center gap-3">
            <div className="w-8 h-8 rounded-lg bg-blue-500/20 flex items-center justify-center">
              <i className="fas fa-ghost text-blue-400 text-sm"></i>
            </div>
            <div>
              <div className="text-sm font-medium text-white">Virtual Bundle</div>
              <div className="text-xs text-slate-400">Pick components separately, assemble at pack</div>
            </div>
          </div>
          <div className="h-8 w-px bg-slate-700"></div>
          <div className="flex items-center gap-3">
            <div className="w-8 h-8 rounded-lg bg-purple-500/20 flex items-center justify-center">
              <i className="fas fa-box text-purple-400 text-sm"></i>
            </div>
            <div>
              <div className="text-sm font-medium text-white">Physical Bundle</div>
              <div className="text-xs text-slate-400">Pre-assembled, has own SKU and inventory</div>
            </div>
          </div>
          <div className="h-8 w-px bg-slate-700"></div>
          <div className="flex items-center gap-3">
            <div className="w-8 h-8 rounded-lg bg-orange-500/20 flex items-center justify-center">
              <i className="fab fa-amazon text-orange-400 text-sm"></i>
            </div>
            <div>
              <div className="text-sm font-medium text-white">FBA Bundle</div>
              <div className="text-xs text-slate-400">Pre-kitted and shipped to Amazon FBA</div>
            </div>
          </div>
        </div>
      </div>

      {/* Bundle Cards */}
      <div className="grid grid-cols-3 gap-6">
        {bundles.map((bundle) => {
          const status = getStatusBadge(bundle);
          const avail = getAvailability(bundle);
          const componentTotal = bundle.components.reduce((sum, c) => {
            const product = getProductById(c.productId);
            return sum + (product?.costs?.base || 0) * c.quantity;
          }, 0);
          const bundlePrice = bundle.prices.shopify;
          const componentMsrpTotal = bundle.components.reduce((sum, c) => {
            const product = getProductById(c.productId);
            return sum + (product?.prices?.msrp || 0) * c.quantity;
          }, 0);
          const savings = componentMsrpTotal - bundlePrice;

          return (
            <div
              key={bundle.id}
              className={`bg-slate-800/50 backdrop-blur border rounded-xl overflow-hidden ${
                status.color === 'emerald' ? 'border-emerald-500/30' :
                status.color === 'amber' ? 'border-amber-500/30' : 'border-red-500/30'
              } ${status.color === 'red' ? 'opacity-80' : ''}`}
            >
              <div className={`px-5 py-4 border-b border-slate-700/50 ${
                status.color === 'emerald' ? 'bg-emerald-500/5' :
                status.color === 'amber' ? 'bg-amber-500/5' : 'bg-red-500/5'
              }`}>
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className="w-12 h-12 rounded-xl bg-amber-500/20 flex items-center justify-center">
                      <i className="fas fa-layer-group text-amber-400 text-xl"></i>
                    </div>
                    <div>
                      <h3 className="font-bold text-white">{bundle.name}</h3>
                      <div className="text-xs text-slate-400">SKU: {bundle.sku}</div>
                    </div>
                  </div>
                  <span className={`px-2 py-1 text-xs rounded-full ${
                    status.color === 'emerald' ? 'bg-emerald-500/10 text-emerald-400' :
                    status.color === 'amber' ? 'bg-amber-500/10 text-amber-400' : 'bg-red-500/10 text-red-400'
                  }`}>
                    {status.text}
                  </span>
                </div>
              </div>
              <div className="p-5 space-y-4">
                {/* Bundle Type Tag */}
                <div className="flex gap-2 flex-wrap">
                  <span className="px-2 py-1 bg-blue-500/10 text-blue-400 text-xs rounded-full flex items-center gap-1">
                    <i className="fas fa-ghost text-[10px]"></i> Virtual
                  </span>
                </div>

                {/* Components */}
                <div>
                  <div className="text-xs text-slate-400 mb-2">COMPONENTS ({bundle.components.length} SKUs)</div>
                  <div className="space-y-2">
                    {bundle.components.slice(0, 3).map((component, idx) => (
                      <div key={idx} className="flex items-center justify-between text-sm">
                        <div className="flex items-center gap-2">
                          <div className={`w-2 h-2 rounded-full ${
                            avail.available > 0 ? 'bg-emerald-400' : 'bg-red-400'
                          }`}></div>
                          <span className="text-slate-300">{getProductName(component.productId)}</span>
                        </div>
                        <span className="text-slate-400"> {component.quantity}</span>
                      </div>
                    ))}
                    {bundle.components.length > 3 && (
                      <div className="text-xs text-slate-500">+{bundle.components.length - 3} more components</div>
                    )}
                  </div>
                </div>

                {/* Availability */}
                <div className={`rounded-lg p-3 ${
                  status.color === 'red' ? 'bg-red-500/10 border border-red-500/20' : 'bg-slate-800/50'
                }`}>
                  {status.color === 'red' ? (
                    <div className="text-center">
                      <div className="text-red-400 font-medium">Cannot Fulfill</div>
                      <div className="text-xs text-slate-400 mt-1">Limiting: {avail.limitingItem}</div>
                    </div>
                  ) : (
                    <div className="grid grid-cols-1 gap-3 text-center text-sm">
                      <div>
                        <div className="text-lg font-bold text-blue-400">{avail.available}</div>
                        <div className="text-xs text-slate-500">Available to Fulfill</div>
                      </div>
                    </div>
                  )}
                </div>

                {/* Pricing */}
                <div className="flex items-center justify-between pt-2 border-t border-slate-700">
                  <div>
                    <div className="text-xs text-slate-400">Bundle Price</div>
                    <div className="text-lg font-bold text-white">{formatCurrencyPrecise(bundlePrice)}</div>
                  </div>
                  <div className="text-right">
                    <div className="text-xs text-slate-400">vs Separate</div>
                    <div className="text-sm text-emerald-400">Save {formatCurrencyPrecise(savings > 0 ? savings : 0)}</div>
                  </div>
                </div>

                <button
                  onClick={() => setSelectedBundle(bundle.id)}
                  className="w-full px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg text-sm"
                >
                  View Details
                </button>
              </div>
            </div>
          );
        })}
      </div>

      {/* Component Usage Table */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
        <div className="px-5 py-4 border-b border-slate-700/50 flex items-center justify-between">
          <div>
            <h2 className="font-semibold text-white">Component Usage Across Bundles</h2>
            <p className="text-xs text-slate-400">Which products are used in which bundles</p>
          </div>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-800/50 border-b border-slate-700/50">
              <tr className="text-left text-xs text-slate-400 uppercase tracking-wider">
                <th className="px-4 py-3 font-medium">Component</th>
                <th className="px-4 py-3 font-medium text-right">Available</th>
                {bundles.slice(0, 3).map(bundle => (
                  <th key={bundle.id} className="px-4 py-3 font-medium text-center">{bundle.name}</th>
                ))}
                <th className="px-4 py-3 font-medium text-right">Total Reserved</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-700/50">
              {/* Get unique product IDs from all bundles */}
              {Array.from(new Set(bundles.flatMap(b => b.components.map(c => c.productId)))).slice(0, 6).map((productId) => {
                const product = getProductById(productId);
                if (!product) return null;

                return (
                  <tr key={productId} className="hover:bg-slate-700/30 transition-colors">
                    <td className="px-4 py-3">
                      <div className="flex items-center gap-2">
                        <div className="w-6 h-6 bg-emerald-500/20 rounded flex items-center justify-center">
                          <i className="fas fa-flask text-emerald-400 text-xs"></i>
                        </div>
                        <span className="text-sm text-white">{product.name}</span>
                      </div>
                    </td>
                    <td className="px-4 py-3 text-right text-sm text-white">
                      {/* We'd need to calculate total inventory - simplified for now */}
                      --
                    </td>
                    {bundles.slice(0, 3).map(bundle => {
                      const component = bundle.components.find(c => c.productId === productId);
                      return (
                        <td key={bundle.id} className="px-4 py-3 text-center text-sm">
                          {component ? (
                            <span className="text-slate-400"> {component.quantity}</span>
                          ) : (
                            <span className="text-slate-500"></span>
                          )}
                        </td>
                      );
                    })}
                    <td className="px-4 py-3 text-right text-sm text-amber-400">0</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>

      {/* Create Bundle Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black/70 backdrop-blur-sm" onClick={() => setShowCreateModal(false)}></div>
          <div className="relative bg-slate-900 border border-slate-700 rounded-2xl w-full max-w-2xl max-h-[90vh] overflow-auto m-4">
            <div className="p-6 border-b border-slate-700 flex items-center justify-between sticky top-0 bg-slate-900 z-10">
              <div>
                <h2 className="text-xl font-bold text-white">Create Bundle</h2>
                <p className="text-sm text-slate-400">Define a new product bundle</p>
              </div>
              <button onClick={() => setShowCreateModal(false)} className="p-2 text-slate-400 hover:text-white hover:bg-slate-800 rounded-lg">
                <i className="fas fa-times"></i>
              </button>
            </div>
            <div className="p-6 space-y-6">
              {/* Basic Info */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm text-slate-400 mb-2">Bundle Name</label>
                  <input type="text" placeholder="e.g., Starter Kit" className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white placeholder-slate-500 focus:outline-none focus:border-amber-500" />
                </div>
                <div>
                  <label className="block text-sm text-slate-400 mb-2">SKU</label>
                  <input type="text" placeholder="e.g., STRKIT-01" className="w-full bg-slate-800 border border-slate-700 rounded-xl px-4 py-3 text-white placeholder-slate-500 focus:outline-none focus:border-amber-500" />
                </div>
              </div>

              {/* Bundle Types */}
              <div>
                <label className="block text-sm text-slate-400 mb-2">Bundle Type</label>
                <div className="grid grid-cols-3 gap-3">
                  <label className="flex items-center gap-3 p-3 bg-slate-800/50 border border-slate-700 rounded-xl cursor-pointer hover:border-blue-500/50 transition-colors">
                    <input type="checkbox" defaultChecked className="rounded bg-slate-700 border-slate-600 text-blue-500" />
                    <div>
                      <div className="text-sm text-white flex items-center gap-2">
                        <i className="fas fa-ghost text-blue-400"></i>
                        Virtual
                      </div>
                      <div className="text-xs text-slate-500">Pick separately</div>
                    </div>
                  </label>
                  <label className="flex items-center gap-3 p-3 bg-slate-800/50 border border-slate-700 rounded-xl cursor-pointer hover:border-purple-500/50 transition-colors">
                    <input type="checkbox" className="rounded bg-slate-700 border-slate-600 text-purple-500" />
                    <div>
                      <div className="text-sm text-white flex items-center gap-2">
                        <i className="fas fa-box text-purple-400"></i>
                        Physical
                      </div>
                      <div className="text-xs text-slate-500">Pre-assembled</div>
                    </div>
                  </label>
                  <label className="flex items-center gap-3 p-3 bg-slate-800/50 border border-slate-700 rounded-xl cursor-pointer hover:border-orange-500/50 transition-colors">
                    <input type="checkbox" className="rounded bg-slate-700 border-slate-600 text-orange-500" />
                    <div>
                      <div className="text-sm text-white flex items-center gap-2">
                        <i className="fab fa-amazon text-orange-400"></i>
                        FBA
                      </div>
                      <div className="text-xs text-slate-500">Ship to Amazon</div>
                    </div>
                  </label>
                </div>
              </div>

              {/* Components */}
              <div>
                <label className="block text-sm text-slate-400 mb-2">Components</label>
                <div className="space-y-2">
                  <div className="flex items-center gap-3 p-3 bg-slate-800/50 border border-slate-700 rounded-xl">
                    <div className="w-10 h-10 bg-emerald-500/20 rounded-lg flex items-center justify-center">
                      <i className="fas fa-flask text-emerald-400"></i>
                    </div>
                    <div className="flex-1">
                      <div className="text-sm text-white">1L Micro Classic</div>
                      <div className="text-xs text-slate-400">124 available</div>
                    </div>
                    <input type="number" defaultValue="1" className="w-16 bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white text-center text-sm" />
                    <button className="p-2 text-slate-500 hover:text-red-400">
                      <i className="fas fa-trash"></i>
                    </button>
                  </div>
                  <button className="w-full p-3 border-2 border-dashed border-slate-700 rounded-xl text-slate-400 hover:text-white hover:border-slate-600 transition-colors">
                    <i className="fas fa-plus mr-2"></i>
                    Add Component
                  </button>
                </div>
              </div>

              {/* Pricing */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm text-slate-400 mb-2">Bundle Price</label>
                  <div className="relative">
                    <span className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400">$</span>
                    <input type="number" defaultValue="59.99" step="0.01" className="w-full bg-slate-800 border border-slate-700 rounded-xl pl-8 pr-4 py-3 text-white focus:outline-none focus:border-amber-500" />
                  </div>
                </div>
                <div>
                  <label className="block text-sm text-slate-400 mb-2">Component Total</label>
                  <div className="px-4 py-3 bg-slate-800/50 border border-slate-700 rounded-xl">
                    <span className="text-slate-400">$45.98</span>
                    <span className="text-emerald-400 ml-2">Save $14.01</span>
                  </div>
                </div>
              </div>

              <div className="flex gap-3 pt-4">
                <button onClick={() => setShowCreateModal(false)} className="flex-1 px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-xl">Cancel</button>
                <button onClick={() => setShowCreateModal(false)} className="flex-1 px-4 py-3 bg-amber-600 hover:bg-amber-500 text-white rounded-xl font-medium">
                  <i className="fas fa-plus mr-2"></i>Create Bundle
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
